.checkbox{cursor:pointer;display:inline-flex;gap:10px;align-items:center}.checkbox__input{position:absolute;opacity:0;width:0;height:0}.checkbox__input:checked+.checkbox__checkbox::after{content:"✓"}.checkbox__input:focus+.checkbox__checkbox{box-shadow:3px 3px 5px rgba(0,0,0,.5)}.checkbox__checkbox{position:relative;width:25px;height:25px;border:1px solid #000}.checkbox__checkbox::after{position:absolute;content:"";font-weight:bold;font-size:1.2rem;top:-2px;left:5px}.checkbox__label{font-size:1.2rem}