.textfield {
  &__input {
    display: block;
    width: 100%;
    padding: 5px 10px;
    font-size: 1.1rem;
    transition: 0.2s;

    &:focus {
      outline: none;
      box-shadow: inset 0px 0px 3px rgba(0, 0, 0, 0.3);
    }
  }

  &__validation {
    margin-top: 5px;
    color: #ff0000;
  }
}
